@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;1,100;1,300&display=swap');
* {
    padding:0;
    margin:0;
    vertical-align:baseline;
    list-style:none;
    border:0
}


.nav {
    max-width: 100%;
    
    .container {
        width: 100%;
        height: 10vh;
        display: flex;
        justify-content: space-between;
        position: fixed;
        background-color: rgba(0, 0, 0, 0.637);
        backdrop-filter:blur(5px);
        z-index: 10;
        font-family: 'Montserrat', sans-serif;

        .row_logo, .row_pagination {
            width: 50%;
            display: flex;
            justify-content: space-between;


        }

        .row_logo {

            h2 {
                margin-left: 3vh;
                margin-top: 3vh;
                color: white;
            }
        }

        .row_pagination {
            margin-right: 5vh;
            ul {
                display: flex;
                margin-top: 2.5vh;
                list-style: none;
                color: white;
                font-size: 20px;
                margin-left: 10%;
                margin-right: 10%;

                li {
                    margin-left: 35%;
                    margin-right: 10%;
                    margin-top: 1vh;
                    width: 50%;

                    a {
                        color: white;
                        text-decoration: none;
                    }
                    a:hover {
                        color: rgba(41, 130, 231, 0.733);
                        transition: .3s ease;
                    }
                }
            }
        }

        .mobile {
            visibility: hidden;
            width: 1px;
        }
    } 
}


@media (max-width:1500px) {
    .row_pagination {
        margin-left: -35vh;

    }
}

@media (max-width:1250px) {

    .container {
        width: 100%;

        .row_pagination {
            visibility: visible;
            width: 100%;
            height: 70vh;
           

            margin-top: 10vh;
            ul {
                display: inline;
                margin-top: 2vh;
                width: 100%;
                height: 70vh;
                backdrop-filter:blur(3px);
                background-color: rgba(0, 0, 0, 0.445);
                border-radius: 15px;
                

                li {
                    width: 100%;
                    height: 10vh;
                    font-size: 25px;
                    margin-left: -2vh;
                    margin-top: 5vh;

                    
                }
            }
        }
        .mobile {
            visibility: visible;
            width: 10vh;
            color: black;

            button {
                border: none;
                background-color: transparent;
            }

            img {
                width: 40px;
                margin-top: 3vh;
                margin-right: 5vh;
            }
        }
    }
    
}

@media (max-width:450px) {

    .container {
        .row_pagination {
            visibility: visible;
            width: 100%;
             
            ul {
                display: inline;
                margin-top: 10vh;
                width: 100%;
                background-color: transparent;

                li {
                    width: 100%;
                    font-size: 25px;
                    border-bottom: 1px solid rgba(0, 0, 0, 0.418);
                    margin-left: -5vh;
                    
                }
            }
        }
    
        .mobile {
            visibility: visible;
            width: 10vh;
            color: rgb(255, 255, 255);

            img {
                img {
                    width: 40px;
                    margin-top: 3vh;
                    margin-right: 5vh;
                }
            }
        }
    }
    
}